/* Tailwind 기본 설정 */
@tailwind base;
@tailwind components;
@tailwind utilities;

/* Header 스타일 */
header {
 position: relative;
 z-index: 10;
 margin-bottom: 2rem;
}

header .flex {
 display: flex;
 justify-content: flex-end;
 align-items: center;
 gap: 2rem;
}

header a {
 display: inline-block;
 text-decoration: none;
 position: relative;
 padding: 0.75rem 2rem;
 background-color: rgba(255, 255, 255, 0.8);
 border-radius: 9999px;
 overflow: hidden;
 transition: transform 0.3s ease;
}

header a:hover {
 transform: scale(1.05);
}

header a.active {
 transform: scale(1.05);
 background-color: rgba(255, 255, 255, 1);
 box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
 transition: all 0.3s ease;
}

/* 버튼 내부 애니메이션 효과 */
header a::before {
 content: '';
 position: absolute;
 top: 0;
 left: -100%;
 width: 100%;
 height: 100%;
 background-color: rgba(112, 112, 112, 0.2);
 z-index: 0;
 transition: all 0.4s ease;
}

header a:hover::before {
 left: 0;
}

/* 버튼 텍스트 스타일 */
header a span {
 position: relative;
 z-index: 1;
 color: #000;
 font-weight: 600;
}

/* Menu Item 스타일 */
.menu-item {
 position: relative;
 overflow: hidden;
 height: 480px;
 display: flex;
 align-items: center;
 z-index: 1;
}

.menu-item .bg-image {
 position: absolute;
 inset: 0;
 filter: grayscale(100%);
 opacity: 0.3;
 transition: opacity 0.3s ease, filter 0.3s ease;
 z-index: 0;
 background-size: cover !important;
 background-position: center !important;
}

.menu-item:hover .bg-image,
.menu-item.active .bg-image {
 filter: grayscale(0%);
 opacity: 0.8;
}

.menu-text {
 font-size: 2.5rem;
 font-weight: 700;
 color: white;
 text-shadow: 
   2px 2px 4px rgba(0, 0, 0, 0.5),
   0 0 20px rgba(255, 255, 255, 0.3);
 position: absolute;
 top: 1.5rem;
 left: 2rem;
 letter-spacing: -0.02em;
 opacity: 0.9;
 transition: transform 0.3s ease, opacity 0.3s ease;
 z-index: 1;
}

.menu-item:hover .menu-text {
 transform: scale(1.02);
 opacity: 1;
}

/* 밑줄 제거 */
.menu-text:hover {
 text-decoration: none;
}

/* Global 스타일 */
body {
 margin: 0;
 font-family: 'Arial', sans-serif;
 background-color: #fff;
}

.main {
 max-width: 800px;
 margin: 0 auto;
 padding: 2rem;
}

/* 모바일 터치 스타일 */
.menu-item.active .bg-image {
 filter: grayscale(0%);
 opacity: 0.8;
}

.menu-item.active .menu-text {
 transform: scale(1.02);
 opacity: 1;
}

@media (max-width: 640px) {
 .menu-item {
   width: 100%;
   height: 200px;
   margin: 0.5rem 0;
 }

 .menu-text {
   font-size: 1.5rem;
   top: 1rem;
   left: 1rem;
 }

 .menu-item:hover {
   opacity: 1;
 }

 .bg-image {
   background-position: center !important;
   background-size: cover !important;
 }
}